import React from "react";
import s from './Post.module.css'

type PostPropsType = {
    message: string
    likeCount: number
}

export const Post: React.FC<PostPropsType> = ({message, likeCount}) => {
    return (
        <div className={s.item}>
            <img
                src="data:image/jpeg;base64,"
                alt="ava"/>
            {message}
            <div>
                <span>like {likeCount}</span>
            </div>
        </div>
    )
}